import { ErrorHandler, NgModule } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { IonicApp, IonicErrorHandler, IonicModule } from "ionic-angular";
import { MyApp } from "./app.component";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { NativeStorage } from "@ionic-native/native-storage";
import { Geolocation } from "@ionic-native/geolocation";
import { NativeGeocoder } from "@ionic-native/native-geocoder";
import { HttpModule } from "@angular/http";
import { AppRate } from "@ionic-native/app-rate";
import { Toast } from "@ionic-native/toast";
import { AppVersion } from "@ionic-native/app-version";
import { UtcDatePipe } from "./pipes/utc";
import { GoogleMaps } from "@ionic-native/google-maps";
import { CallNumber } from "@ionic-native/call-number";
import { Contacts } from "@ionic-native/contacts";
import { Diagnostic } from "@ionic-native/diagnostic";
import { OpenNativeSettings } from "@ionic-native/open-native-settings";
import { Network } from "@ionic-native/network";
import { Push } from "@ionic-native/push";
import { Device } from "@ionic-native/device";
import { SocialSharing } from "@ionic-native/social-sharing";
import { Keyboard } from "@ionic-native/keyboard";
import { DatePicker } from '@ionic-native/date-picker';
import { HTTP } from '@ionic-native/http';


import { SignUp } from "../pages/SignUp/SignUp";
import { Login } from "../pages/LoginIn/Login";
import { OrderConfirmation } from "../pages/OrderConfirmationDialog/orderconfirmation";
import { MapView } from "../pages/MapView/MapView";
import { WelcomePage } from "../pages/WelcomePage/Welcome";
import { ConfirmOrder } from "../pages/ConfirmOrderPage/ConfirmOrderPage";
import { NotificationPage } from "../pages/NotificationTemplate/NotificationPage";
import { TranslateLoader, TranslateModule } from "@ngx-translate/core";
import { TranslateHttpLoader } from "@ngx-translate/http-loader";
import { HttpClient, HttpClientModule } from "@angular/common/http";
import { AboutPage } from "../pages/MyOrders/about";
import { ContactPage } from "../pages/MyAccount/contact";
import { GetService } from "./services/get.servie";
import { Utils } from "./services/Utils";
import { ModalController } from 'ionic-angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

import { SocketIoModule, SocketIoConfig } from 'ng-socket-io';
const config: SocketIoConfig = { url: GetService.TRACKING_URL, options: {} };

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    UtcDatePipe,
    SignUp,
    Login,
    OrderConfirmation,
    MapView,
    WelcomePage,
    ConfirmOrder,
    NotificationPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/AboutUs/AboutUs.module.ngfactory#MyAboutUsPageModuleNgFactory', name: 'AboutUs', segment: 'AboutUs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/autocomplete/Autocomplete.module.ngfactory#AutocompleteModuleNgFactory', name: 'AutocompletePage', segment: 'Autocomplete', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cancel-order/cancel-order.module.ngfactory#CancelOrderPageModuleNgFactory', name: 'CancelOrderPage', segment: 'cancel-order', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/addupdaterides/addupdaterides.module.ngfactory#AddupdateridesPageModuleNgFactory', name: 'AddupdateridesPage', segment: 'addupdaterides', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/category-services/category-services.module.ngfactory#CategoryServicesPageModuleNgFactory', name: 'CategoryServicesPage', segment: 'category-services', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/FeedbackPage/Feedback.module.ngfactory#FeedbackPageModuleNgFactory', name: 'Feedback', segment: 'Feedback', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-address/edit-address.module.ngfactory#EditAddressPageModuleNgFactory', name: 'EditAddressPage', segment: 'edit-address', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/filter-contacts/filter-contacts.module.ngfactory#FilterContactPageModuleNgFactory', name: 'FilterContactsPage', segment: 'filter-contacts', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/InviteFreinds/InviteFriends.module.ngfactory#InviteFriendsPageModuleNgFactory', name: 'InviteFriends', segment: 'InviteFriends', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/modal/modal.module.ngfactory#ModalPageModuleNgFactory', name: 'ModalPage', segment: 'modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/MyPayments/my-payment.module.ngfactory#MyPaymentPageModuleNgFactory', name: 'MyPaymentPage', segment: 'my-payment', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/MyProfile/myprofile.module.ngfactory#MyProfilePageModuleNgFactory', name: 'MyProfile', segment: 'myprofile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/myrides/myrides.module.ngfactory#MyridesPageModuleNgFactory', name: 'MyridesPage', segment: 'myrides', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/OrderDetailsPage/orderdetails.module.ngfactory#OrderDetailsPageModuleNgFactory', name: 'OrderDetails', segment: 'orderdetails', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/points/points.module.ngfactory#PointsPageModuleNgFactory', name: 'PointsPage', segment: 'points', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/PreviewPage/PreviewPage.module.ngfactory#PreviewPageModuleNgFactory', name: 'PreviewPage', segment: 'PreviewPage', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/products/products.module.ngfactory#ProductsPageModuleNgFactory', name: 'ProductsPage', segment: 'products', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/raise-complain/raise-complain.module.ngfactory#RaiseComplainPageModuleNgFactory', name: 'RaiseComplainPage', segment: 'raise-complain', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ScheduleOrder/ScheduleOrder.module.ngfactory#ScheduleOrderPageModuleNgFactory', name: 'ScheduleOrderPage', segment: 'ScheduleOrder', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/simpledialog/simpledialog.module.ngfactory#SimpledialogPageModuleNgFactory', name: 'SimpledialogPage', segment: 'simpledialog', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/trackorder/trackorder.module.ngfactory#TrackorderPageModuleNgFactory', name: 'TrackorderPage', segment: 'trackorder', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpClientModule, FormsModule, ReactiveFormsModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    SocketIoModule.forRoot(config)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    SignUp,
    Login,
    OrderConfirmation,
    MapView,
    WelcomePage,
    ConfirmOrder,
    NotificationPage,
  ],
  providers: [
    
    StatusBar,
    GetService,
    Utils,
    AppVersion,
    NativeStorage,
    Geolocation,
    AppRate,
    Contacts,
    CallNumber,
    NativeGeocoder,
    Diagnostic,
    Toast,
    Keyboard,
    GoogleMaps,
    OpenNativeSettings,
    Network,
    DatePicker,
    SocialSharing,
    Device,
    Push,
    HTTP,
    SplashScreen,
    ModalController,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule {
}



